<template>
  <div>
    <div class="banner"></div>
    <div class="newsWrapper">
      <div class="newsTableHead">
        <div
          @click="changeView('/Newscenter/industry')"
          :class="{'Industry':true, 'active':this.r=='/Newscenter/industry'}"
        >行业新闻</div>
        <div
          @click="changeView('/Newscenter/online')"
          :class="{'online':true, 'active':this.r=='/Newscenter/online'}"
        >在线公告</div>
      </div>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  mounted() {
    this.r = this.$route.path;
  },
  methods: {
    changeView(p) {
      this.$router.push(p);
      this.r = p;
    }
  },
  data() {
    return {
      r: ""
    };
  }
};
</script>

<style lang="less" scoped>
.banner {
  background: url("../../assets/img_news/banner.png") no-repeat;
  height: 67px;
  padding: 574px 0 358px 0;
  margin-bottom: 43px;
}
.newsWrapper {
  width: 1200px;
  margin: 0 auto;
}
.newsTableHead {
  display: flex;
  justify-content: space-between;
  div {
    width: 570px;
    height: 138px;
    line-height: 138px;
    background-color: #f3f3f3;
    font-size: 48px;
    color: #333333;
    text-align: center;
  }
}
.active {
  background-image: url("../../assets/img_news/menu.png");
  background-size: 100% 100%;
  width: 578px !important;
  height: 158px !important;
  background-color: transparent !important;
}
</style>